.how-did-it-get-this-way h2 {
  text-align: center;
}

.fact {
  text-align: center;
}

.story {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
}

.story-part {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 10px;
}

.story-part .text p {
  margin-top: 0;
  font-size: 14px;
}

.story-part .mapboxgl-map {
  height: 250px;
  width: 550px;
  border: 1px solid grey;
}

.graph {
  margin-left: auto;
  margin-right: auto;
}

figcaption {
  font-size: 12px;
}